// Fixes the parsing of the unusual front matter / SCSS file.
*{};

//
// Reset
//
ul {
	line-height: unset;
	padding-left: unset;
}

//
// Variables
//
$mobile-max-width: 700px;
$gap: 1.25rem;

$element-meta-planning-bar-width: 15%;
$element-meta-ongoing-bar-width: 50%;

//
// Functions
//
@mixin is-mobile() {
	@media screen and (max-width: #{$mobile-max-width}) {
		@content;
	}
}

//
// Mixins
//
@mixin is-dark() {
	@media screen and (prefers-color-scheme: dark) {
		@content;
	}
}

@mixin is-light() {
	@media screen and (prefers-color-scheme: light) {
		@content;
	}
}

//
// General
//
html {
	scroll-behavior: smooth;
	@media (prefers-reduced-motion) {
		scroll-behavior: auto;
	}
}

// Priorities container.
.priorities-container {
	font-family: "Montserrat", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
	background-color: var(--card-background-color);
	overflow: hidden;

	.banner {
		.container {
			padding-top: 7rem;
			padding-bottom: 7rem;

			.last-updated {
				display: flex;
				flex-direction: row;

				.label {
					&::after {
						content: "Â ";
					}
				}
				.date {

				}
			}
		}
	}

	.intro {
		.wishlist-btns {
			display: flex;
			flex-direction: row;
			justify-content: space-around;
		}
	}

	.navigation {
		li {
			list-style: none;
			margin-bottom: 0;
		}

		.navigation-list {
			padding-left: 0;
			margin-bottom: 0;
			display: flex;
			flex-direction: row;
			justify-content: space-between;
			gap: $gap;

			> li {
				list-style: none;
				flex-grow: 1;
				width: 100%;
			}
		}
	}

	.priorities {
		// i.e. "core", "rendering", or "physics"
		.category {
			& > .container {
				.category-title {
					display: flex;
					flex-direction: row;
					gap: $gap;

					border-radius: 15px;
					padding: 1rem 2rem;
					letter-spacing: -2px;
					font-weight: 600;

					// .category.level-1
					@at-root #{selector-replace(&, ".category", ".category.level-1")} {
						background-color: var(--primary-color);
						font-size: 3rem;
					}
					// .category.level-2
					@at-root #{selector-replace(&, ".category", ".category.level-2")} {
						background-color: var(--background-color);
						font-size: 2.125rem;
					}

					.category-title-text {
						flex-grow: 1;
						width: 100%;
						color: white;

						> p {
							margin: 0;
						}
					}

					.category-title-anchor {
						flex-grow: 0;
						text-decoration: none;
						visibility: hidden;
						color: white;

						&:focus {
							outline: none;
						}

						// On .category-title:hover
						@at-root #{selector-replace(&, ".category-title", ".category-title:hover")} {
							visibility: visible;
						}
					}
				}

				.category-content {
					.element-list {
						padding-left: 0;
						margin-bottom: 0;

						> li {
							list-style: none;
							margin-bottom: 0;
						}

						.element {
							display: grid;
							grid-template-areas:
								"meta main";
							grid-template-columns: minmax(10vw, 15%) auto;
							grid-template-rows: auto;
							gap: $gap;

							border-top: 2px solid var(--primary-color);
							padding: 1.25rem 0;

							@include is-mobile() {
								grid-template-areas:
									"meta"
									"main";
								grid-template-columns: auto;
								grid-template-rows: auto;
							}

							.element-meta {
								grid-area: meta;

								.element-stage {
									display: flex;
									flex-direction: column;
									align-items: center;

									.preview-bar {
										contain: paint;
										height: 1rem;
										width: 100%;
										border-radius: 999999px;
										background-color: var(--background-color);

										.progress {
											position: absolute;
											top: 0;
											left: 0;
											height: 100%;

											background-color: var(--primary-color);
											border-top-right-radius: 9999px;
											border-bottom-right-radius: 9999px;

											// Planning.
											@at-root #{selector-replace(&, ".element-stage", ".element-stage.planning")} {
												width: $element-meta-planning-bar-width;
											}

											// On going.
											@at-root #{selector-replace(&, ".element-stage", ".element-stage.ongoing")} {
												width: $element-meta-ongoing-bar-width;
											}

											// Done.
											@at-root #{selector-replace(&, ".element-stage", ".element-stage.done")} {
												width: 100%;
												border-top-right-radius: 0;
												border-bottom-right-radius: 0;
											}
										}
									}
								}

								.element-last-updated {
									.label {
										// Default state.
										.last-updated {
											display: inline;
										}
										.recently-updated {
											display: none;
											font-weight: 600;
										}

										// If update is recent.
										@at-root #{selector-replace(&, ".element-last-updated", ".element-last-updated.recent")} {
											.last-updated {
												display: none;
											}
											.recently-updated {
												display: inline;
											}
										}
									}

									.value {

									}
								}
							}

							.element-main {
								grid-area: main;

								height: 100%;

								.element-title {
									display: flex;
									flex-direction: row;
									align-items: stretch;
									gap: $gap;

									list-style: none;
									cursor: pointer;
									border-radius: 15px;

									margin-top: -$gap;
									padding: $gap;

									&:focus {
										outline: none;
									}

									&:hover {
										background-color: var(--background-color);
									}

									// Gross hack to make the element title height consistent when toggled.
									height: 100%;
									@at-root #{selector-replace(&, ".element-main", ".element-main[open]")} {
										// padding-bottom: $gap * 2;
										// margin-bottom: -$gap;
										height: auto;
									}

									.element-title-anchor {
										flex-grow: 0;

										display: flex;
										flex-direction: row;
										align-items: center;

										text-decoration: none;

										&:focus {
											outline: none;
										}
									}

									.element-title-text {
										flex-grow: 1;
										width: 100%;

										display: flex;
										flex-direction: row;
										align-items: center;

										font-weight: 700;

										> p {
											margin: 0;
										}
									}

									.element-accordion-toggle {
										flex-grow: 0;

										display: flex;
										flex-direction: row;
										align-items: center;

										.less {
											display: none;
										}
										.more {
											display: block;
										}

										@at-root #{selector-replace(&, ".element-main", ".element-main[open]")} {
											.less {
												display: block;
											}
											.more {
												display: none;
											}
										}
									}
								}

								.element-content {
									padding: 0 $gap;

									.element-description {
										font-size: 1rem;
									}

									.element-details {
										font-size: 0.9rem;

										ul {
											padding-left: 1.5rem;

											li {
												margin-bottom: 0.45rem;
											}
										}

										> ul {
											// Force reset the style for the first list.
											list-style-type: disc;
										}

										> ol {
											// Force reset the style for the first list.
											counter-reset: element-details-list-counter;
										}
									}
								}
							}
						}
					}
				}
			}
		}
	}
}
